<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Tips Dan Materi</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <br /><br />
      <!-- Segment Control -->
      <!-- <ion-segment v-model="selectedSegment" mode="ios" class="segment-margin">
        <ion-segment-button value="materi">
          <ion-label>Materi</ion-label>
        </ion-segment-button>
        <ion-segment-button value="latihan">
          <ion-label>Latihan</ion-label>
        </ion-segment-button>
      </ion-segment> -->

      <ion-content class="ion-padding">
        <!-- Loading Indikator -->
        <div v-if="isLoading" class="loading-container">
          <ion-spinner name="crescent"/>
          <p>Memuat Data...</p>
        </div>

        <!-- No Data Found -->
         <div v-else-if="isDataEmpty" class="empty-container">
          <ion-text color="danger">
            <p>Ops, data tidak ditemukan</p>
          </ion-text>
         </div>
        <ion-grid v-if="selectedSegment === 'materi'">
          <ion-row>
            <ion-col
              size="12"
              size-md="6"
              v-for="materi in filteredMateriList"
              :key="materi.id"
            >
              <ion-card
                style="background-color: #3b5998; margin-top: -20px;"
                @click="openDetailModal(materi.content)"
              >
                <ion-card-header>
                  <ion-card-title style="color: whitesmoke">{{
                    materi.title
                  }}</ion-card-title>
                </ion-card-header>
                <ion-card-content>
                  <p style="color: whitesmoke">{{ materi.subjectName }}</p>
                </ion-card-content>
              </ion-card>
            </ion-col>
          </ion-row>
        </ion-grid>

        <ion-grid v-if="selectedSegment === 'latihan'">
          <ion-row>
            <ion-col
              size="12"
              size-md="6"
              v-for="latihan in filteredLatihanList"
              :key="latihan.id"
            >
              <ion-card
                class="custom-card"
                style="background-color: #ffcccb"
                @click="checkExamStatus(latihan)"
              >
                <ion-card-header>
                  <ion-card-title>{{ latihan.subjectName }}</ion-card-title>
                </ion-card-header>
                <ion-card-content>
                  <p>Kelas: {{ latihan.className }}</p>
                  <p>Durasi: {{ latihan.durasi }} Menit</p>
                  <p>Tanggal Ujian: {{ formatDate(latihan.tanggalUjian) }}</p>
                </ion-card-content>
              </ion-card>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-content>

      <ion-modal :is-open="isDetailModalOpen" @did-dismiss="closeDetailModal">
        <ion-header>
          <ion-toolbar>
            <ion-title>Detail Materi</ion-title>
            <ion-buttons slot="end">
              <ion-button @click="closeDetailModal">Close</ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <div v-html="detailContent"></div>
        </ion-content>
      </ion-modal>

      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  ionSpinner,
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonGrid,
  IonRow,
  IonCol,
  IonCard,
  IonCardHeader,
  IonCardTitle,
  IonCardContent,
  // IonSegment,
  // IonSegmentButton,
  IonModal,
  IonButton,
  IonButtons,
  IonToast,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";
import {
  getFirestore,
  collection,
  getDocs,
  doc,
  getDoc,
} from "firebase/firestore";
import { getAuth } from "firebase/auth";
import { firebaseApp } from "../firebase.js";

const isLoading = ref(false);
const isDataEmpty = ref(false);

const router = useRouter();
const route = useRoute();
const db = getFirestore(firebaseApp);
const auth = getAuth(firebaseApp);
const classes = ref([]);
const userClass = ref("");
const materiList = ref([]);
const filteredMateriList = ref([]);
const filteredLatihanList = ref([]); // For latihan list
const isDetailModalOpen = ref(false);
const detailContent = ref("");
const subjects = ref([]);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});

const selectedSegment = ref("materi"); // Default ke segment "Materi"

onMounted(async () => {
  await fetchUserClass();
  await fetchClasses();
  await fetchSubjects();
  await fetchMateri();
  await fetchJadwalUjian(); // Fetch latihan data dan filter langsung di dalam fungsi ini
  filterMateriList(); // Filter materi setelah data diambil
});

const fetchUserClass = async () => {
  const user = auth.currentUser;
  if (user) {
    const userDoc = await getDoc(doc(db, "users", user.uid));
    if (userDoc.exists()) {
      userClass.value = userDoc.data().kelas;
    } else {
      showToast("Kelas pengguna tidak ditemukan.", "danger");
    }
  } else {
    showToast("Pengguna tidak terautentikasi.", "danger");
  }
};

const fetchClasses = async () => {
  const querySnapshot = await getDocs(collection(db, "kelas"));
  classes.value = querySnapshot.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));
};

const fetchSubjects = async () => {
  const querySnapshot = await getDocs(collection(db, "mataPelajaran"));
  subjects.value = querySnapshot.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));
};

const fetchMateri = async () => {
  try {
    isLoading.value = true;
    const querySnapshot = await getDocs(collection(db, "materi"));
    materiList.value = querySnapshot.docs.map((doc) => {
      const data = doc.data();
      return {
        id: doc.id,
        ...data,
        className:
          classes.value.find((kelas) => kelas.id === data.class)?.name || "N/A",
        subjectName:
          subjects.value.find((subject) => subject.id === data.subject)?.name ||
          "N/A",
      };
    });

    console.log("Materi List:", materiList.value);
    filterMateriList(); // Panggil filterMateriList setelah data materi diambil
    console.log("Filtered Materi List:", filteredMateriList.value);
    isDataEmpty.value = filteredMateriList.value.length === 0;
  } catch (error) {
    console.error("Error fetching materi:", error);
    showToast("Terjadi kesalahan saat mengambil data materi.", "danger");
  }finally{
    isLoading.value = false; 
  }
};

const fetchJadwalUjian = async () => {
  try {
    isLoading.value = true;
    const querySnapshot = await getDocs(collection(db, "jadwal"));
    const allJadwalUjian = querySnapshot.docs.map((doc) => {
      const data = doc.data();
      return {
        id: doc.id,
        ...data,
        className:
          classes.value.find((kelas) => kelas.id === data.kelas)?.name || "N/A",
        subjectName:
          subjects.value.find((subject) => subject.id === data.mataPelajaran)
            ?.name || "N/A",
        durasi: data.durasi,
        tanggalUjian: data.tanggalUjian,
        soal: data.soal || [],
        aktif: data.aktif,
        kelas: data.kelas, // ID kelas
        mataPelajaran: data.mataPelajaran, // ID mata pelajaran
      };
    });

    // Terapkan filter dinamis berdasarkan className (kelas pengguna) dan mataPelajaran (dari route)
    filteredLatihanList.value = allJadwalUjian.filter(
      (latihan) =>
        latihan.className ===
          classes.value.find((k) => k.id === userClass.value)?.name && // Sesuai dengan kelas pengguna saat ini
        latihan.mataPelajaran === route.params.materiId // Sesuai dengan ID mata pelajaran dari route
    );

    console.log("Filtered Latihan List:", filteredLatihanList.value); // Untuk debugging
  } catch (error) {
    console.error("Error fetching jadwal ujian:", error);
    showToast("Terjadi kesalahan saat mengambil data ujian.", "danger");
  }
  finally{
    isLoading.value = false;
  }
};
const checkExamStatus = (jadwal) => {
  router.push({ name: "halamanujianUser", params: { jadwalId: jadwal.id } });
};
const formatDate = (timestamp) => {
  if (!timestamp) return "N/A";
  let date;
  if (timestamp.seconds) {
    date = new Date(timestamp.seconds * 1000);
  } else if (typeof timestamp === "string" || timestamp instanceof Date) {
    date = new Date(timestamp);
  } else {
    return "Invalid Date";
  }
  return date.toLocaleDateString("id-ID", {
    year: "numeric",
    month: "short",
    day: "numeric",
  });
};

const filterMateriList = () => {
  const materiId = route.params.materiId; // Dapatkan materiId dari route
  filteredMateriList.value = materiList.value.filter(
    (materi) => materi.subject === materiId && materi.class === userClass.value // Filter berdasarkan subject ID dan kelas
  );
};

const openDetailModal = (content) => {
  detailContent.value = content;
  isDetailModalOpen.value = true;
};

const closeDetailModal = () => {
  isDetailModalOpen.value = false;
};

const doRefresh = (event) => {
  setTimeout(() => {
    fetchMateri();
    fetchJadwalUjian();
    filterMateriList();
    event.target.complete();
  }, 2000);
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};
</script>

<style scoped>
.segment-margin {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  width: calc(100% - 30px);
}

.loading-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Mengisi seluruh tinggi layar */
  text-align: center;
}

.loading-container ion-spinner {
  margin-bottom: 10px; /* Spasi antara spinner dan teks */
  --width: 50px;
  --height: 50px;
  animation: rotate 1.2s linear infinite; /* Animasi putaran spinner */
}

.empty-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
  color: #888;
}

/* Optional: Animasi loading */
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>
